.about__background {
  margin-top: 15px;
  background: url(../../../public/paragraph_sample.jpg) no-repeat center;
  background-size: cover;
  position: relative;
  height:800px;
}

.about__background::before {
  background-color: rgba(114, 114, 114, 0.5);
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  content: ' ';
}

.about__box {
  position: absolute;
  width: 100%;
  text-align: center;
  top:45%;
  left:50%;
  transform:translateY(-50%)translateX(-50%);
  transform:translateY(-50%)translateX(-50%);
  margin:auto;
}

.about__about-text {
  text-align: center;
  color: white;
  font-weight: 800;
  font-size: 25px;
  top: 0;
  left: 0;
}
